import React, { useState } from "react";

import { Card, Grid, Form, Input } from "semantic-ui-react";

import TxButton from "./TxButton";

export default function Extrinsics(props) {
  const { api, accountPair } = props;

  const [status, setStatus] = useState("");

  const initialState = {
    tokenId: ""
  };

  const [formState, setFormState] = useState(initialState);
  const { tokenId } = formState;

  const onChange = (_, data) => {
    setFormState(formState => {
      return {
        ...formState,
        [data.state]: data.value
      };
    });
  };

  return (
    <Grid.Column>
      <Form>
        <Card>
          <Card.Content>
            <Card.Header>Deposit Token</Card.Header>
            <Card.Description>
              <Form.Field>
                <Input
                  onChange={onChange}
                  label="Token ID"
                  fluid
                  placeholder="ID of Token you control"
                  state="tokenId"
                  type="text"
                />
              </Form.Field>
            </Card.Description>
          </Card.Content>
          <Card.Content extra>
            <Form.Field>
              <TxButton
                api={api}
                accountPair={accountPair}
                label={"Add"}
                params={[tokenId]}
                setStatus={setStatus}
                tx={api.tx.plasmaCash.deposit}
              />
              {status}
            </Form.Field>
          </Card.Content>
        </Card>
      </Form>
    </Grid.Column>
  );
}
